.title__container {
    text-align: center;
    padding: 20px;
    border-bottom: black;
  }
  .title {
    font-size: 36px;
    font-weight: bold;
  }
  
  .span {
    margin-left: 10px;
    margin-bottom: 10px;
    padding-top: 10px;
  }
  .main {
    display: flex;
    flex-direction: column;
    /* width: 1000px; */
  }
  .main__container {
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
  }
  .sum__container {
    margin-top: 40px;
    text-align: center;
    font-size: 20px;
  }
  .left {
    min-width: 283px;
  }
  .btn__container {
    margin-top: 30px;
    padding: 10px 0;
  }
  .tab {
    list-style: none;
    padding: 8px 25px;
    font-weight: lighter;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 5px;
  }
  
  .tab__active {
    background: #4caf50;
    color: #fff;
  }
  .tabs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-family: "Roboto", sans-serif;
    /* margin-top: 20px; */
  }
  